import Vue from 'vue'
import Router from 'vue-router'
/*import DetailPage from '../detail-page/index.vue';
import DetailVideo from '../detail-video/index.vue';
import SmallVideo from '../small-video/index.vue';*/
import Pc from '../pc/index.vue';
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Pc',
      component: Pc
    },
      {
          path: '/index',
          name: 'Pc',
          component: Pc
      }
  ]
})



